video.hero-bg {
     object-fit: cover;
     width: 100%;
     height: 100%;
     position: fixed;
     z-index: -1;
}

img.hero-bg {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

 .hero-container {
     /* background: url('../assets/images/paracosms_gif_compressed2.gif') center center/cover no-repeat; */
     width: 100%;
     height: 100vh;  
     max-height: -webkit-fill-available;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.4);
     object-fit: contain;
 }

 .hero-container > h1 {
     color: #fff;
     font-size: 48px;
     text-align: center;
     width: 75%;
     margin-top: 80px;
 }

 .hero-container > p {
     margin-top: 8px;
     color: #fff;
     font-size: 18px;
     text-align: center;
     width: 75%;
 }

 .hero-small-caption {
    margin-top: 20px;
    color: #fff;
    font-size: 18px;
    text-align: center;
    width: 75%;
    font-style: italic;
 }

 .hero-btns {
     margin-top: 32px;
 }

 .hero-btns .btn {
    margin: 6px;
}

 .fa-play-circle {
     margin-left: 4px;
 }

.countdown-timer-wrapper {
    margin-top: 32px;
    justify-content: center;
    border: 1px solid #fff;
    padding: 8px 20px 8px 20px;
    box-shadow: 0 6px 15px rgba(255, 255, 255, 0.322);
}

.countdown-timer-wrapper > p {
    text-align: center;
    justify-content: center;
    color: #fff;
}

.countdown-timer {
    text-align: center;
    justify-content: center;
    font-size: 32px;
    font-style: bold;
}


 @media screen and (max-width: 960px) {
     .hero-container > h1 {
         font-size: 48px;
     }

     .hero-container > p {
        font-size: 16px;
    }
 }

 @media screen and (max-width: 768px) {
    .hero-container > h1 {
        font-size: 40px;
    }

    .hero-container > p {
        font-size: 14px;
    }

    .btn-mobile {
        display: block;
        text-decoration: none ;
    }

    .btn {
        width: 100%;
    }
   
 }

 @media screen and (max-width: 480px) {
    .hero-container > h1 {
        font-size: 32px;
    }
    
    .hero-container > p {
        font-size: 12px;
    }
 }