.roadmap-container {
    background: #010101;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
}


.roadmap-wrapper {
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 100px;
}

.roadmap-wrapper > h1 {
    color: #fff;
    font-size: 40px;
    text-align: center;
    width: 75%;
    margin-bottom: 50px;
}

.roadmap-wrapper > p {
    margin-top: 50px;
    color: #fff;
    font-size: 18px;
    text-align: center;
}

@media screen and (max-width: 768px) {
   .roadmap-wrapper > h1 {
       font-size: 30px;
   }

   .roadmap-wrapper > p {
       font-size: 18px;
   }
}

@media screen and (max-width: 480px) {
    .roadmap-wrapper > h1 {
       font-size: 25px;
   }
   
   .roadmap-wrapper > p {
       font-size: 14px;
   }
}

.vertical-timeline-element-content {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25),
      0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
    padding: 1em 2em 1em 2em !important;
  }