.about-container {
    background: #010101;
    width: 100%;
    /* height: 100vh; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
}

.about-wrapper {
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 100px;
}

.about-wrapper > h1 {
    color: #fff;
    font-size: 40px;
    text-align: center;
    width: 75%;
}

.about-wrapper > p {
    margin-top: 50px;
    color: #fff;
    font-size: 18px;
    text-align: center;
}

@media screen and (max-width: 768px) {
    .about-wrapper > h1 {
        font-size: 30px;
    }
 
    .about-wrapper > p {
        font-size: 18px;
    }
 }
 
 @media screen and (max-width: 480px) {
     .about-wrapper > h1 {
        font-size: 25px;
    }
    
    .about-wrapper > p {
        font-size: 14px;
    }
 }