.footer-container {
    background-color: #010101;
    padding: 1rem 0 1rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.social-media {
    max-width: 1000px;
    width: 100%;
    align-items: center;
}

.social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: auto;
}

.social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80px;
    margin: 16px 16px;
}

.footer-logo {
    display: flex;
    align-items: center;
}

.social-logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-self: center;
    margin: 16px 16px;
    font-family: 'Abril Fatface', cursive;
}

.website-rights-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 16px 16px;
}

.website-rights {
    color: #fff;
    align-self: center;
    /* align-items: center; */
}

@media screen and (max-width: 820px) {
    .footer-links {
        padding-top: 2rem;
    }

    .footer-input {
        width: 100%;
    }

    .btn {
        width: 100%;
    }

    .footer-link-wrapper {
        flex-direction: column;
    }

    .social-media-wrap {
        flex-direction: column;
    }

}