.whitepaper-container {
    background: #010101;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
}

.whitepaper-wrapper {
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    margin: auto;
    margin-bottom: 100px;
}

.whitepaper-wrapper > h1 {
    color: #fff;
    font-size: 20px;
    text-align: center;
    margin-top: 80px;
    width: 75%;
}

.whitepaper-wrapper > p {
    margin-top: 20px;
    color: #fff;
    font-size: 16px;
    text-align: left;
}

.whitepaper-header-logo-wrapper {
    width: 100%;
    top: 0px;
    position: sticky;
    background: #010101;
}

.whitepaper-header-logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    /* align-self: center; */
    margin: 16px 16px;
    font-family: 'Abril Fatface', cursive;
}