:root {
    --primary: #fff;
    --solana-gradient: linear-gradient(45deg, 
    rgba(220,31,255,1) 0%, 
    rgba(3,225,255,1) 70%, 
    rgba(0,255,163,1) 100%);
    --avax-red: #e84142;
}

.btn {
    padding: 8px 20px;
    border-radius: 10px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--primary:hover {
    border: 0px;
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--outline:hover {
    border: 0px;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover {
    background: var(--avax-red);
    color: #fff;
    transition: all 0.3s ease-out;
}

.arrow {
    margin-left: 8px;
    font-size: 15px;
}