.faq-container {
    background: #fff;
    /* height: 100vh; */
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* object-fit: contain; */
}

.faq-title-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px;
    align-items: center;
}

.faq-title-wrapper > h1 {
    color: #010101;
    font-size: 40px;
    text-align: center;
    width: 75%;
}

.faq-collapsible-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 50px;
}

.faq-collapsible-inner-wrapper {
    box-shadow: 2px 10px 35px 1px rgba(153,153,153,0.3);
    width: 75%;
}

.Collapsible {
    justify-content: center;
    width: 100%;
}

/* The content within the collaspable area */
.Collapsible__contentInner {
    padding: 20px 15px 20px 15px;
    border: 1px solid rgba(153,153,153,0.3);
    border-top: 0;
}

.Collapsible__contentInner > p {
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 12px;
    line-height: 20px;
}

.Collapsible__contentInner > p:last-child {
    margin-bottom: 0px;
}

/* The link which when clicked opens the collapsable area */
.Collapsible__trigger {
  display: block;
  font-weight: 800;
  font-size: 14px;
  text-decoration: none;
  position: relative;
  border: 1px solid white;
  padding: 10px 30px 10px 10px;
  background: #e84142;
  color: white;
}

.Collapsible__trigger:after {
    font-family: "Font Awesome 5 Free";
    content: '\f078';
    position: absolute;
    right: 10px;
    top: 10px;
    display: block;
    transition: transform 300ms;
}

.Collapsible__trigger.is-open:after {
    transform: rotateZ(180deg);
}

@media screen and (max-width: 768px) {
    .faq-title-wrapper > h1 {
        font-size: 30px;
    }
}
 
@media screen and (max-width: 480px) {
    .faq-title-wrapper > h1 {
        font-size: 25px;
    }
    
    .Collapsible__trigger {
        font-size: 14px;
    }
    
    .Collapsible__contentInner > p {
        font-size: 12px;
    }
}

