.mint-form-container {
    display: flex;
    justify-content: center;
}

.mint-form-wrapper {
    display: 'flex';
    flex-direction: column;
    width: 75%;
}

.mint-form-wrapper > p {
    color: #000;
    font-size: 12px;
    margin-top: 15px;
}

.mint-input-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.mint-input-wrapper > p {
    color:black;
    font-size: 14px;
    margin-top: 3px;
    margin-bottom: 3px;
}

.mint-input {
    text-align: center;
    height: 40px;
    border-radius: 10px;
}

.mint-submit-btn {
    margin-top: 10px;
    height: 40px;
    background-color: #e84142;
    color: #fff;
    font-weight: bold;
    border-radius: 10px;
    text-align: center;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    margin-bottom: 30px;
}

.mint-submit-btn:hover {
    transition: all 0.2s ease-in-out;
    background: #e84142;
    border: 0px;
}

.mint-caption {
    color: #000;
    font-size: 12px;
    margin-bottom: 30px;
    text-align: center;
}

.wallet-btn {
    border-radius: 10px;
    background: #e84142;
    white-space: nowrap;
    padding: 10px 22px;
    color: #fff;
    font-size: 16px;
    outline: none;
    border: 2px solid #e84142;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    font-weight: bold;
    position: fixed;
    top: 20px;
    z-index: 100;
    right: 20px;
}

.wallet-btn:hover {
    transition: all 0.2s ease-in-out;
    border: none;
}

@media screen and (max-width: 768px) {
    .wallet-btn {
        display: none;
    }
}